.card-container {
    background: linear-gradient(
        to left,
        #210133 0%,
        rgb(1, 36, 33) 100%
    );
    border: 1px solid black;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
    backface-visibility: hidden;
    transform: translateZ(42);
    transition: transform 0.25s ease-out;
    color: bisque;

}

.card-container:hover {
    transform: rotate(5deg);
  }
  